// -------------------
// Trending Bar Styles
// -------------------

.Trend {
  &__TrendingBar {
    @include display-flex(space-between, start, row);
    overflow-x: scroll;
    &__element {
      margin: 10px;
      box-shadow: 0px 0px 15px $dark-red;
      position: relative; // ! for MovieDataWrapper child
    }
  }
}
